.card {
  box-sizing: border-box;
  background-color: #fff;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  flex-direction: column;
  width: 200px;
  padding: 0 16px;
  display: flex;
}

.card__img {
  justify-content: center;
  align-items: center;
  margin-bottom: 0;
  display: flex;
}

.card__item {
  background-image: url("imac.202c52ff.jpeg");
  background-size: contain;
  width: 160px;
  height: 134px;
  margin-top: 32px;
  margin-bottom: 40px;
  display: block;
}

.card__title {
  color: #060b35;
  margin: 0;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__code {
  color: #616070;
  margin-top: 4px;
  margin-bottom: 0;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.card__price {
  color: #616070;
  font-size: 12px;
  font-weight: 400;
  line-height: 18px;
  position: relative;
}

.card__price:after {
  content: "$2,199";
  color: #060b35;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
  position: absolute;
  right: 0;
}

.card__buy {
  margin: 16px 0;
  display: flex;
}

.card__bottom {
  color: #fff;
  text-transform: uppercase;
  text-align: center;
  background-color: #00acdc;
  border-radius: 5px;
  width: 166px;
  height: 40px;
  font-size: 14px;
  font-weight: 700;
  line-height: 40px;
  text-decoration: none;
}

a[data-qa="hover"]:hover {
  box-sizing: border-box;
  color: #00acdc;
  background-color: #fff;
  border: 1px solid #00acdc;
  border-radius: 5px;
}

.stars {
  flex-direction: row;
  justify-content: flex-start;
  margin-top: 16px;
  margin-bottom: 24px;
  margin-right: 0;
  padding-inline-start: 0;
  list-style-type: none;
  display: flex;
}

.stars__star {
  vertical-align: middle;
  background-image: url("star.822d561e.svg");
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
  display: inline-block;
}

.stars__star:last-child {
  margin-right: 0;
  position: relative;
}

.stars__star:last-child:after {
  content: "Reviews: 5";
  color: #060b35;
  white-space: nowrap;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
  position: absolute;
  top: 50%;
  right: -70px;
  transform: translateY(-50%);
}

.stars--4 .stars__star:nth-child(-n+4) {
  background-image: url("star-active.edda4c43.svg");
}

body {
  margin: 0;
  font-family: Roboto, sans-serif;
}
